<template>
  <div>
    <div class="title">
      <div class="left" @click="go">返回</div>
      {{$route.name}}
      <div v-show="$route.path == '/index/mine'" class="right" @click="$router.push('/login')">退出</div>
    </div>
    <router-view class="main"></router-view>
  </div>
</template>

<script>
export default {
  methods: {
    go() {
      this.$router.go(-1);
    }
  }
};
</script>
<style scoped lang="stylus">
@import './stylus/index.styl';

.main {
  margin-top: .79rem;
}

.title {
  position: fixed;
  top: 0;
  width: 100vw;
  background: $primary;
  color: $font-color5;
  line-height: $h5;
  text-align: center;
  font-size: $h2;
  z-index: 1;
}


.left {
  position: absolute;
  left: .3rem;
}
.right {
  position: absolute;
  right: .3rem;
  top 0
}
</style>


