import Vue from 'vue'
import Router from 'vue-router'
let login = () => import("../pages/login/login.vue")
let index = () => import("../pages/index/index.vue")
let detail = () => import("../pages/detail/detail.vue")
let catelist = () => import("../pages/catelist/catelist.vue")
let register = () => import("../pages/register/register.vue")
let home = () => import("../pages/home/home.vue")
let mine = () => import("../pages/mine/mine.vue")
let cate = () => import("../pages/cate/cate.vue")
let search = () => import("../pages/search/search.vue")
let shop = () => import("../pages/shop/shop.vue")


Vue.use(Router)
let router = new Router({
  routes: [
    {
      path: "/index",
      component: index,
      children: [
        {
          path: "home",
          name: "首页",
          component: home
        },
        {
          path: "shop",
          name: "购物车",
          component: shop
        },
        {
          path: "cate",
          name: "分类",
          component: cate
        },
        {
          path: "mine",
          name: "个人中心",
          component: mine
        },
        {
          path: "",
          redirect: "home"
        }
      ]
    },
    {
      path: "/login",
      name: "登录",
      component: login
    },
    {
      path: "/register",
      name: "注册",
      component: register
    },
    {
      path: "/catelist",
      name: "分类列表",
      component: catelist
    },

    {
      path: "/detail",
      name: "详情页",
      component: detail
    },
    {
      path: "/search",
      nname: "搜索页",
      component: search
    },
    {
      path: "*",
      component: login
    },


  ]
})
router.beforeEach((to,from,next)=>{
  if(to.path=="/login" || to.path== "/register"){
    next()
    return
  }
  if(localStorage.getItem("user")){
    next()
    return
  }
    next("/login")

})
export default router



